<template>
  <v-container
      id="input-usage"
  >

    <v-card id="card" class="my-16">
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
      >

        <div id="title_div" :class="['text-h4', 'pa-2', 'font-weight-bold']">IBD01</div>

        <div v-for="batch in fullBatch" :key="batch">
          <v-row>
            <v-col cols="3" id="title_column">
              <v-list-subheader>{{batch}} :</v-list-subheader>
            </v-col>

            <v-col cols="9">
              <v-text-field
                  clearable
                  label="Number"
                  type="number"
                  variant="outlined"
                  hide-details=auto
                  :rules="inputRules"
              >
              </v-text-field>
            </v-col>
          </v-row>
        </div>
      </v-form>

      <!--   Implement a function that validation, disabled button   -->
      <v-btn
          block
          class="mr-4"
          color="blue lighten-1"
          style="margin-top: 12px"
          :disabled="!valid"
          @click="validate"
      >
        submit
      </v-btn>

    </v-card>


  </v-container>
</template>

<script>
export default {
  data() {
    return {
      valid: true,
      fullBatch: ["U1","U2","U3","D1","D2","D3"],
      inputRules: [
        v => !!v || 'Cannot be empty',
        v => (v && v > 0) || 'Number cannot be negative.',
      ],
    }
  },
  methods: {
    async validate() {
      const { valid } = await this.$refs.form.validate()

      if(valid) alert('Data is submitted.')
    },
  },
}
</script>

<style>
.v-list-subheader {
  font-size: 18px;
}

#card {
  margin: 10px;
  padding: 25px;
}

#title_div {
  text-align: center;
}

